import {Injectable, Type} from "@angular/core";
import {IGoldenLayoutComponentState} from "../models/golden-layout-component-state";
import {GoldenLayoutComponent} from "../components/golden-layout/golden-layout.component";

export interface AddComponentData {
    layoutItemName: string;
    state: any;
    parent?: any;
}

@Injectable()
export class LayoutManagerService {
    private _layout: GoldenLayoutComponent;

    get layout(): GoldenLayoutComponent {
        return this._layout;
    }

    setLayout(layout: GoldenLayoutComponent) {
        this._layout = layout;
    }

    addComponent(data: AddComponentData) {
        if (this.layout) {
            this.layout.addComponent(data.layoutItemName, data.state, data.parent);
        }
    }

    addComponentAsColumn(data: AddComponentData) {
        if (this.layout) {
            this.layout.addComponentAsColumn(data.layoutItemName, data.state);
        }
    }

    loadState(state: IGoldenLayoutComponentState, fireStateChanged: boolean = false) {
        if (this.layout) {
            this.layout.loadState(state, fireStateChanged);
        }
    }

    clear() {
        if (this.layout) {
            this.layout.clear();
        }
    }
}
