import {Observable, Subject} from 'rxjs';
import ItemConfig = GoldenLayoutNamespace.ItemConfig;
import {IGoldenLayoutComponentSettings} from './models/configuration';
import ComponentConfig = GoldenLayoutNamespace.ComponentConfig;
import {GoldenLayoutComponent} from './components/golden-layout/golden-layout.component';
import {JsUtils} from './utils/JsUtils';

export type PopinHandler = (componentConfig: ComponentConfig) => void;
export type PopupStateChangedHandler = () => void;

export interface IPopupWindowManagerConfig {
  componentConfig: ItemConfig;
  layoutSettings: IGoldenLayoutComponentSettings;
  popinHandler: PopinHandler;
  popupStateChangedHandler: PopupStateChangedHandler;
  popupClosedHandler: (closedByUser: boolean) => void;
  runChangeDetectionHandler: () => void;
}

export interface IPopupWindowConfig {
  componentConfig: any;
  layoutSettings: any;
  popin: PopinHandler;
  fireStateChanged: PopupStateChangedHandler;
  runChangeDetectionInRootWindow: () => void;
  needRunChangeDetection: Observable<any>;
  onLayoutCreated: (GoldenLayoutComponent) => void;
}

export const PopupWindowConfigKey = 'popupWindowConfigKey';

export class PopupWindowManager {
  id: string = JsUtils.generateGUID();

  private _closedByUser: boolean = true;
  private _runChangeDetection$ = new Subject();
  private _popupLayout: GoldenLayoutComponent;

  static openWindow(url: string): Window {
    const windowOptions = {
      width: 500,
      height: 500,
      menubar: 'no',
      toolbar: 'no',
      location: 'no',
      personalbar: 'no',
      resizable: 'yes',
      scrollbars: 'no',
      status: 'no'
    };

    const popupWindowNative = window.open(url,
      `${JsUtils.generateGUID()}`, PopupWindowManager.serializeWindowOptions(windowOptions));

    if (popupWindowNative == null) {
      return null;
    }

    return popupWindowNative;
  }

  static serializeWindowOptions(windowOptions: any) {
    const windowOptionsString = [];

    for (const key in windowOptions) {
      windowOptionsString.push(key + '=' + windowOptions[key]);
    }

    return windowOptionsString.join(',');
  }

  constructor(public window: Window,
              private popupConfig: IPopupWindowManagerConfig) {

    this.window[PopupWindowConfigKey] = {
      componentConfig: popupConfig.componentConfig,
      layoutSettings: popupConfig.layoutSettings,

      popin: ((compConfig) => {
        popupConfig.popinHandler(compConfig);
      }) as PopinHandler,

      fireStateChanged: (() => {
        popupConfig.popupStateChangedHandler();
      }) as PopupStateChangedHandler,

      runChangeDetectionInRootWindow: () => {
        popupConfig.runChangeDetectionHandler();
      },

      needRunChangeDetection: this._runChangeDetection$.asObservable(),
      onLayoutCreated: (layout: GoldenLayoutComponent) => {
        this._popupLayout = layout;
      }
    } as IPopupWindowConfig;

    $(this.window).one('load', () => {
      $(this.window).one('unload', () => {
        popupConfig.popupClosedHandler(this._closedByUser);
        this.window.close();
      });
    });
  }

  runChangeDetection() {
    this._runChangeDetection$.next();
  }

  saveState() {
    const componentConfig = this._popupLayout.saveState().content[0] as ComponentConfig;
    delete componentConfig['width'];

    return componentConfig;
  }

  close() {
    this._closedByUser = false;
    this.window.close();
  }
}
