import Container = GoldenLayoutNamespace.Container;
import {Observable, Subject, Subscription} from 'rxjs';
import {Injector} from "@angular/core";
import {GoldenLayoutContainer} from "./tokens/golden-layout-container.token";
import {GoldenLayoutComponent} from "./components/golden-layout/golden-layout.component";
import {takeUntil} from "rxjs/operators";
import {IGoldenLayoutItem} from './models/golden-layout-item';

export class GoldenLayoutItem implements IGoldenLayoutItem {
    protected _container: Container;
    protected _goldenLayoutComponent: GoldenLayoutComponent;
    protected _tabElement: JQuery;
    private _titleSubscription: Subscription;
    private _destroy = new Subject();

    constructor(injector: Injector) {
        this._container = injector.get(GoldenLayoutContainer) as Container;
        this._goldenLayoutComponent = injector.get(GoldenLayoutComponent);
    }

    saveState(): any {
        return null;
    }

    onResize() {
    }

    onShow() {
    }

    onHide() {
    }

    onTabCreated(tabElement: JQuery) {
        this._tabElement = tabElement;
    }

    onContainerMaximized(isOwnContainer: boolean) {
    }

    onContainerMinimized(isOwnContainer: boolean) {
    }

    setTitle(title$: Observable<string>) {
        if (this._titleSubscription) {
            this._titleSubscription.unsubscribe();
        }

        this._titleSubscription = title$
            .pipe(takeUntil(this._destroy))
            .subscribe((title: string) => {
                this._container.setTitle(title);
            });
    }

    fireStateChanged() {
        this._goldenLayoutComponent.fireStateChanged();
    }

    ngOnDestroy() {
      this._destroy.next();
      this._destroy.complete();
    }
}
