import {
  Component, ComponentFactory, ComponentFactoryResolver,
  Inject,
  Injector,
  Type,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
// import Container = GoldenLayoutNamespace.Container;
import {GoldenLayoutContainer} from "../../tokens/golden-layout-container.token";
import {GoldenLayoutItemComponentResolver} from "../../tokens/golden-layout-item-component-factory.token";
import {Observable, of} from "rxjs";
import {GoldenLayoutItem} from "../../golden-layout-item";
import {
  ComponentResolver,
  IComponentFactoryResolver,
  IComponentTypeResolver,
  IGoldenLayoutComponentConfiguration
} from '../../models/configuration';
import {GoldenLayoutComponentConfiguration} from '../../tokens/golden-layout-configuration.token';
import {DefaultLabels} from '../../components/golden-layout/golden-layout.component';
import {IGoldenLayoutItem} from '../../models/golden-layout-item';

enum LoadComponentState {
  Loading,
  Success,
  Failed
}

@Component({
    selector: 'golden-layout-item-container',
    templateUrl: 'golden-layout-item-container.component.html',
    styleUrls: ['golden-layout-item-container.component.scss']
})
export class GoldenLayoutItemContainerComponent extends GoldenLayoutItem {
    @ViewChild("componentContainer", {read: ViewContainerRef, static: true}) componentContainer;
    private _layoutItem: IGoldenLayoutItem;
    protected _tabElement: JQuery;
    loadComponentState: LoadComponentState;
    LoadComponentState = LoadComponentState;

    get loadingLabel(): Observable<string> {
      return (this._configuration.labels && this._configuration.labels.loading)
        ? this._configuration.labels.loading
        : DefaultLabels.loading;
    }

    get failedToLoadLabel(): Observable<string> {
      return (this._configuration.labels && this._configuration.labels.failedToLoadComponent)
        ? this._configuration.labels.failedToLoadComponent
        : DefaultLabels.failedToLoadComponent;
    }

    constructor(@Inject(GoldenLayoutContainer) protected _container: any,
                @Inject(GoldenLayoutItemComponentResolver) private _componentResolver: ComponentResolver,
                @Inject(GoldenLayoutComponentConfiguration) private _configuration: IGoldenLayoutComponentConfiguration,
                private _componentFactoryResolver: ComponentFactoryResolver,
                private _injector: Injector) {
        super(_injector);
    }

    ngOnInit() {
        this.setTitle(of('')); // fix
        this.setTitle(this.loadingLabel);

        if ((this._componentResolver as IComponentFactoryResolver).factory != null) {
          this.loadComponentState = LoadComponentState.Loading;

          (this._componentResolver as IComponentFactoryResolver).factory()
            .subscribe({
              next: (factory: ComponentFactory<any>) => {
                this.setTitle(of(this._componentResolver.componentName));
                const ref = this.componentContainer.createComponent(factory, undefined, this._injector);
                ref.changeDetectorRef.detectChanges();

                this._layoutItem = ref.instance as IGoldenLayoutItem;

                if (this._tabElement) {
                  this._layoutItem.onTabCreated(this._tabElement);
                }

                this.loadComponentState = LoadComponentState.Success;
              },
              error: (e) => {
                console.error(e);
                this.loadComponentState = LoadComponentState.Failed;
                this.setTitle(this.failedToLoadLabel);
              }
            });
        } else {
          this.setTitle(of(this._componentResolver.componentName));

          const componentFactory = this._componentFactoryResolver.resolveComponentFactory((this._componentResolver as IComponentTypeResolver).component);
          const ref = this.componentContainer.createComponent(componentFactory, undefined, this._injector);
          ref.changeDetectorRef.detectChanges();

          this._layoutItem = ref.instance as IGoldenLayoutItem;

          if (this._tabElement) {
            this._layoutItem.onTabCreated(this._tabElement);
          }

          this.loadComponentState = LoadComponentState.Success;
        }
    }

    saveState() {
        if (this._layoutItem) {
            return this._layoutItem.saveState();
        }

        return {};
    }

    onResize() {
        if (this._layoutItem) {
            this._layoutItem.onResize();
        }
    }

    onShow() {
        if (this._layoutItem) {
            this._layoutItem.onShow();
        }
    }

    onHide() {
        if (this._layoutItem) {
            this._layoutItem.onHide();
        }
    }

    onTabCreated(tabElement: JQuery) {
        if (this._layoutItem) {
            this._layoutItem.onTabCreated(tabElement);
        }

        this._tabElement = tabElement;

    }

    onContainerMaximized(isOwnContainer: boolean) {
        if (this._layoutItem) {
            this._layoutItem.onContainerMaximized(isOwnContainer);
        }
    }

    onContainerMinimized(isOwnContainer: boolean) {
        if (this._layoutItem) {
            this._layoutItem.onContainerMinimized(isOwnContainer);
        }
    }

    ngOnDestroy() {
    }

}
