import {ApplicationRef, Component, Inject, Injectable, Injector, OnInit, SkipSelf, ViewChild} from '@angular/core';
import {IGoldenLayoutComponentConfiguration, IGoldenLayoutComponentSettings} from '../../models/configuration';
import {GoldenLayoutComponent} from '../../components/golden-layout/golden-layout.component';
import {takeUntil} from 'rxjs/operators';
import {
  IPopupWindowConfig,
  PopupWindowConfigKey
} from '../../popup-window-manager';
import ComponentConfig = GoldenLayoutNamespace.ComponentConfig;
import {GoldenLayoutComponentConfiguration} from '../../tokens/golden-layout-configuration.token';
import {Subject} from 'rxjs';

@Injectable()
export class __Holder {
  constructor(@SkipSelf() @Inject(GoldenLayoutComponentConfiguration) public configuration) {
  }
}


export function configurationFactory(injector: Injector, holder: __Holder) {
  const configuration = holder.configuration;
  const popupConfiguration = (window as any)[PopupWindowConfigKey] as IPopupWindowConfig;

  return {
    settings: {
      ...configuration.settings,
      popinHandler: (componentConfig: ComponentConfig) => {
        delete componentConfig['width'];
        popupConfiguration.popin(componentConfig);
      }
    },
    labels: configuration.labels,
    components: configuration.components
  } as IGoldenLayoutComponentConfiguration;
}

@Component({
  selector: 'golden-layout-popup',
  templateUrl: './golden-layout-popup.component.html',
  styleUrls: ['./golden-layout-popup.component.scss'],
  providers: [
    __Holder,
    {
      provide: GoldenLayoutComponentConfiguration,
      useFactory: configurationFactory,
      deps: [
        Injector,
        __Holder
      ]
    }
  ]
})
export class GoldenLayoutPopupComponent implements OnInit {
  @ViewChild(GoldenLayoutComponent, {static: false}) layout: GoldenLayoutComponent;

  private _popupConfiguration: IPopupWindowConfig;
  private _componentConfig: any;
  private _suppressChangeDetection: boolean = false;
  private _destroy = new Subject();

  constructor(private _appRef: ApplicationRef) {
    const popupConfiguration = (window as any)[PopupWindowConfigKey] as IPopupWindowConfig;

    popupConfiguration.needRunChangeDetection
      .pipe(
        takeUntil(this._destroy)
      )
      .subscribe(() => {
        this._suppressChangeDetection = true;
        this._appRef.tick();
        this._suppressChangeDetection = false;
      });

    this._popupConfiguration = popupConfiguration;
    this._componentConfig = popupConfiguration.componentConfig;
  }

  ngOnInit() {
  }

  ngDoCheck() {
    if (!this._suppressChangeDetection) {
      this._popupConfiguration.runChangeDetectionInRootWindow();
    }
  }

  ngAfterViewInit() {
    this.layout.loadState(GoldenLayoutComponent.getSingleComponentLayoutConfig(this._componentConfig));
    this.layout.$stateChanged
      .pipe(takeUntil(this._destroy))
      .subscribe(() => {
        if (this.layout.getAllComponents().length === 0) {
          window.close();
          return;
        }

        this._popupConfiguration.fireStateChanged();
      });

    this._popupConfiguration.onLayoutCreated(this.layout);
  }

  ngOnDestroy() {
    this._destroy.next();
    this._destroy.complete();
  }
}
